import { mxResources } from "@edp-pmf/mxgraph-ts";
import { Constant } from "../util/Constant";
import { ResourceKeys } from "../util/ResourceKeys";
import { ToolbarDropDownButton } from "./ToolbarDropDownButton";
import { ToolbarItemDisplayStyle } from "./ToolbarItemDisplayStyle";
import { ToolbarItems } from "./ToolbarItems";
import { ToolbarSeparator } from "./ToolbarSeparator";

/**
 * 默认的工具栏列表
 */
export class DefaultToolbarItems extends ToolbarItems {
    constructor() {
        super();
        this.init();
    }

    /**
     * 初始化
     */
    private init() {
        const btnMainMenu = new ToolbarDropDownButton();
        btnMainMenu.resourceKey = ResourceKeys.pmf_mainMenu;
        btnMainMenu.displayStyle = ToolbarItemDisplayStyle.Image;
        btnMainMenu.imageSrc = Constant.IMAGE_PATH + "/iconic/svg/menu.svg";
        btnMainMenu.imageClassName = "pmf_iconic_svg";
        btnMainMenu.toolTipText = mxResources.get(ResourceKeys.pmf_mainMenu);
        this.itemDict[ResourceKeys.pmf_mainMenu] = btnMainMenu;
        this.itemList.push(btnMainMenu);

        this.itemList.push(new ToolbarSeparator());

        const btnZoom = new ToolbarDropDownButton();
        btnZoom.displayStyle = ToolbarItemDisplayStyle.Text;
        btnZoom.text = "100%";
        btnZoom.resourceKey = ResourceKeys.pmf_zoom;
        btnZoom.toolTipText = mxResources.get(ResourceKeys.pmf_zoom);
        this.itemDict[ResourceKeys.pmf_zoom] = btnZoom;
        this.itemList.push(btnZoom);
    }
}